import React, { Component } from "react";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import Banner from "../components/Banner";
import img from "../imgs/tempImg.jpg";

class FastStartTrips extends Component {
  render() {
    return (
      <div>
        <Banner bg={img} text="FAST START PROGRAM" />
        <MDBContainer style={{ marginTop: "60px" }} className="pb-5">
          <MDBRow>
            <MDBCol>
              <p className="text-center">2020 Fast Start Program</p>
              <p className="text-center">
                Earn a two-night vacation... TWICE in 2020!
              </p>
              ​
              <p className="text-center">
                National Mutual Benefit has added two additional vacation trips
                to the mix for 2020. Agents and managers are eligible to earn
                <strong>both</strong> Fast Start trips if you meet the qualifications for each
                cycle of the Fast Start Program. The Fast Start Program is
                broken into two cycles - <strong>Fast Start to 2020</strong> and <strong>End of Summer
                Push</strong>. Fast Start Program Trips are solely based on Applications,
                New Members and NAFYC. Trip Credits are excluded from the Fast
                Start Program. We can't wait to send you on these getaways!
              </p>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </div>
    );
  }
}

export default FastStartTrips;
