import React from "react";
import { MDBContainer, MDBRow, MDBCol, MDBIcon, MDBNavLink } from "mdbreact";

import Logo from "../imgs/nmb_icon.png";

const Footer = () => {
  return (
    <div className="footer">
      <MDBContainer className="py-5">
        <MDBRow>
          <MDBCol md="3">
          </MDBCol>
          <MDBCol md="6" className="text-center ">
            <h5 className="font-weight-bold">
              <span className="mr-3">CONNECT WITH US:</span>
              <MDBIcon fab icon="facebook-square" className="mx-1" />
              <MDBIcon fab icon="instagram" className="mx-1" />
              <MDBIcon fab icon="linkedin" className="mx-1" />
              <MDBIcon far icon="envelope" className="mx-1" />
            </h5>
          </MDBCol>
          <MDBCol md="3">
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="text-center mt-4 mb-5">
            <img src={Logo} alt="NMB Logo" />
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol>
            <p className="text-center">
              National Mutual Benefit | 6522 Grand Teton Plaza | Madison WI
              53719-2912 | 1 (800) 779-1936 or 1 (608) 833-1936
            </p>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </div>
  );
};

export default Footer;
